import Master from '../componentes/Master';
import fetch from 'isomorphic-unfetch';
import Precio from '../componentes/Precio';
import Noticias from '../componentes/Noticias';
import Eventos from '../componentes/Eventos';


const index = (props) => (
    <Master>
        <div className="row">
            <div className="col-12">
                <h2>Precio del Bitcoin</h2>
                <Precio 
                    precio = {props.precioBitcoin}
                />
            </div>
            <div className="col-md-8">
                <h2 className="mb-3">Noticias del Bitcoin</h2>
                <Noticias 
                    noticias = {props.noticias}
                />
            </div>
            <div className="col-md-4">
                <h2>Proximos Eventos Bitcoin</h2>
                <Eventos 
                    eventos= {props.eventos}
                />
            </div>
        </div>
    </Master>
);

index.getInitialProps = async () => {

    const urlBitcoin = `https://api.coinmarketcap.com/v2/ticker/1/`;
    const precio = await fetch(urlBitcoin);
    const resPrecio = await precio.json();

    const urlNoticias = `https://newsapi.org/v2/everything?q=bitcoin&sortBy=publishedAt&apiKey=aa5536b31cbf4bf1b5cf4a96bc8938c2&language=es`;
    const noticias = await fetch(urlNoticias);
    const resNoticias = await noticias.json();

    const token = '6LSRMHFVJKNHDIMGQENV';
    const ordenar = "date";
    const idioma = "es_ES";

    const urlEventos =`https://www.eventbriteapi.com/v3/events/search/?q=Bitcoin&sort_by=${ordenar}&location.address=Argentina&token=${token}&locale=${idioma}`;
    const eventos = await fetch(urlEventos);
    const resEventos = await eventos.json();


    return {
        precioBitcoin: resPrecio.data.quotes.USD,
        noticias: resNoticias.articles,
        eventos: resEventos.events
    }

}

export default index;